<template>
  <div class="main">
	   <div v-html="state.headerText"></div>
     <textarea ref="scriptInput" id="scriptInput" @input="processInput()"></textarea>
  </div>
</template>

<script>
export default {
  name: 'Main',
  props: [ 'state' ],
	data(){
		return {
		}
	},
	methods:{
		refreshTokens(){
			if(!this.state.tokens.length) return
			let sendData = {'tokens': this.state.tokens.map(v=>{
			  return v.token
			})}
			//console.log(JSON.stringify(sendData))
			//return
			fetch('https://'+this.state.rootDomain+'/refreshTokens.php', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(sendData),
			}).then(res=>res.json()).then(data=>{
				if(data[0]){
					console.log(data[1])
				}
			})
		},
		processInput(){
			let script = this.state.scriptInput.value
			let tokens = [], token = ''
			script.split('').map(v=>{
				 if((/[a-zA-Z]/).test(v) || (/[0-9]/).test(v) || v == '_' || v == '-'){
					 token += v
				 }else{
					 tokens=[...tokens, token]
           token = ''
				 }
			})
      if(token) tokens=[...tokens, token]
			if(tokens.length){
				if(this.state.tokens.length){
          tokens.map((v, i)=>{
						if(this.state.tokens.length>i){
							if(this.state.tokens[i].token != v){
								this.state.tokens[i].token = v
								this.state.tokens[i].stale = true
							}
						}else{
							this.state.tokens = [...this.state.tokens, {
								token: v,
								stale: true
							}]
						}
					})
			  }else{
				  this.state.tokens = tokens
					this.state.tokens = this.state.tokens.map(v=>{
						return {
							token: v,
							stale: true
						}
					})
				}
			} else {
				this.state.tokens = []
			}
			this.refreshTokens()
		}
	},
	mounted(){
		this.state.scriptInput = this.$refs['scriptInput']
		this.state.scriptInput.focus()
	}
}
</script>

<style scoped>
.main{
  margin: 10px;
	padding: 10px;
	border: 1px solid #4f86;
}
#scriptInput{
  background: #333;
	color: #ff0;
  width: calc(100% - 5px);
	height: calc(100vh - 70px);
}
#scriptInput:focus{
  outline: none;
}
</style>
